<template>
  <div id="ArcGIS" class="map" />
</template>

<script>
// 引入ArcGIS
import { loadModules } from "esri-loader";
export default {
  name: "map",
  data: () => ({
    ArcGIS: {},
  }),
  mounted() {
    this.myArcGIS();
  },
  methods: {
    myArcGIS() {
      // 资源配置
      const options = {
        url: "https://js.arcgis.com/4.15/",
        css: "https://js.arcgis.com/4.15/esri/themes/light/main.css",
      };
      // 按需模块加载
      loadModules(
        [
          "esri/Map",
          "esri/Graphic",
          "esri/views/MapView",
          "esri/widgets/BasemapToggle",
        ],
        options
      ).then(([Map, Graphic, MapView, BasemapToggle]) => {
        // 地图类型配置
        const map = new Map({
          basemap: "osm",
        });
        // 地图渲染配置
        const view = new MapView({
          map,
          zoom: 13,
          container: "ArcGIS",
          center: [116.31, 39.9258],
        });
        // 地图切换配置
        const toggle = new BasemapToggle({
          view,
          nextBasemap: "hybrid",
        });
        // 地图实例添加切换样式
        view.ui.add(toggle, "top-right");
        // 地图视图监听图层创建完毕事件的回调
        view.on("layerview-create", () => {
          // 点坐标配置
          const geometry = {
            type: "point",
            latitude: 39.9258,
            longitude: 116.31,
          };
          // 符号配置
          const symbol = {
            url:
              "https://static.arcgis.com/images/Symbols/Shapes/BlackStarLargeB.png",
            type: "picture-marker",
            width: 40,
            height: 40,
          };
          // 绘制图形并用创建的符号来呈现点标记
          const markGraphic = new Graphic({
            symbol,
            geometry,
            attributes: {
              name: "温湿度传感器",
              location: "主干廊综合舱-防火段1-温湿度传感器",
            },
            popupTemplate: {
              title: "{name}",
              content: `<div>{location}</div>`,
            },
          });
          // 地图图形化实例添加新图形
          view.graphics.add(markGraphic);
        });
        // 几何位置配置
        const polyline = {
          type: "polyline",
          paths: [
            [116.39, 39.92],
            [116.25, 39.93],
          ],
        };
        // 几何样式配置
        const lineSymbol = {
          width: 4,
          type: "simple-line",
          color: [0, 150, 136],
        };
        // 实例几何图形
        const polylineGraphic = new Graphic({
          symbol: lineSymbol,
          geometry: polyline,
          attributes: {
            Age: "2020",
            Name: "Keystone",
            Owner: "TransCanada",
            Message: "Normal Operation",
          },
          popupTemplate: {
            title: "设备详情信息:",
            content: [
              {
                type: "fields",
                fieldInfos: [
                  {
                    fieldName: "Age",
                  },
                  {
                    fieldName: "Name",
                  },
                  {
                    fieldName: "Owner",
                  },
                  {
                    fieldName: "Message",
                  },
                ],
              },
            ],
          },
        });
        // 地图实例添加几何图形
        view.graphics.add(polylineGraphic);
        // 储存实例
        this.ArcGIS = view;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.map {
  height: 100%;
}
</style>
